.friend-card-image {
    background-position: center center;
    background-size: cover; }

.fb-button {
    background-color: #2D4373;
    color: white;
    width: 100%;
    justify-content: flex-start; }

.twt-button {
    background-color: #2795E9;
    color: white;
    width: 100%;
    justify-content: flex-start; }

.fb-button:hover {
    color: whitesmoke; }

.twt-button:hover {
    color: whitesmoke; }

.full-width-button {
    width: 100%; }

.bigger-font-label {
    font-size: 16px; }

.footer {
    padding: 2rem 1.5rem 2rem; }

#social {
    -webkit-box-align: center;
    align-items: center;
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 1em;
    -webkit-box-pack: center;
    justify-content: center; }

#social a {
    display: inline-block;
    font-size: 11px;
    height: 20px;
    line-height: 20px;
    margin: 5px; }

#social iframe {
    margin: 5px; }

.profile-shelves-section {
    padding: 0.5rem 1.5rem; }
